import React, { useState } from "react";

function App() {
  let [count, setCount] = useState(0);

  function incriment() {
    setCount(count + 1);
  }

  function decrement() {
    setCount(count - 1);
  }

  return (
    <div className="container">
      <h1>{count}</h1>

      <button onClick={decrement}>-</button>
      <button onClick={incriment}>+</button>
    </div>
  );
}

export default App;
